/* Footer Section  */

footer {
    background-color: var(--mainColor);
}

.footer-container {
    max-width: 1200px;
    color: white;
    margin: auto;
    padding: 2rem;
}

.footer-container,
ul {
    display: flex;
    justify-content: space-between;
    align-items: start;
    flex-wrap: wrap;
}

.common-footer-box {
    margin-top: 2rem;
}

.common-footer-box h3 {
    margin-bottom: 1rem;
}

footer ul {
    flex-direction: column;
    align-items: start;
    list-style-type: none;
    gap: .3rem;
}

footer .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .5rem;
    margin-top: .5rem;
}

footer .logo .fa-brands {
    font-size: 1.5rem;
}

footer .common-footer-box p:nth-child(2) {
    margin: .5rem 0 .5rem 0;
}