/* Header Section  */
header,
nav,
nav ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
}

header {
    padding: 1rem;
    background-color: var(--mainColor);
    color: white;
    height: 50px;
    z-index: 9999;
}

nav {
    max-width: 1200px;
    width: 100%;
    margin: auto;
}


nav div img {
    width: 13rem;
}

ul {
    gap: 1rem;
    flex-wrap: wrap;
}

.bar {
    position: relative;
}

.bar::after {
    position: absolute;
    left: 0;
    bottom: -2px;
    content: "";
    border-bottom: 1px solid white;
    width: 60%;
}

.fa-bars {
    display: none;
}

@media (max-width: 1000px) {
    header nav ul {
        position: absolute;
        background-color: whitesmoke;
        left: -100%;
        overflow: hidden;
        width: 100%;
        flex-direction: column;
        transition: all 0.3s ease-in-out;
        top: 82px;
        opacity: 0;
    }

    .toggle-bar {
        left: 0;
        opacity: 1;
        z-index: 9999;
    }

    header ul li {
        text-align: center;
        padding: 1rem 0 1rem 0;
        width: 100%;
    }

    .bar::after {
        display: none;
    }

    header ul li:hover {
        background-color: rgba(204, 200, 200, 0.425)
    }

    header ul a {
        color: black;
    }

    .fa-bars {
        display: block;
        font-size: 1.10rem;
        transition: all 0.3s ease-in-out;
        cursor: pointer;
    }

    .fa-bars:active {
        transform: rotate(180deg);
    }
}