/*------------------- 
    Main Section 
--------------------*/

main {
    background-color: whitesmoke;
}

.hero-section {
    max-width: 1200px;
    padding: 10rem 2rem 20rem 2rem;
    margin: auto;
    position: relative;
}

.hero-section h1 {
    font-size: 3vw;
    color: var(--pink);
    padding: 0 0 1rem 0;
}

.hero-section p {
    z-index: 1;
    margin-bottom: 2rem;
}

.btn {
    margin-top: 2rem;
    background-color: var(--mainColor);
    padding: .5rem 2rem .5rem 2rem;
    border-radius: 10px;
    font-weight: bold;
    font-size: 1.2rem;
}

.btn:hover {
    background-color: #015361ab;
}

.hero-section img {
    position: absolute;
    max-width: 800px;
    right: 2rem;
    bottom: 6rem;
    z-index: 0;
}

.hr {
    width: 100%;
    height: 2px;
    background-color: rgba(128, 128, 128, 0.171);
    position: relative;
}

.hr-img {
    position: absolute;
    right: 20%;
    width: 80px;
    top: -40px;
}


/* Course Section  */
.course-container {
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    padding: 5rem 2rem 5rem 2rem;
}


.course-img-conatiner {
    text-align: center;
    width: 100%;
    margin-top: 1rem;
}

.course-img-conatiner img {
    width: 70%;
    margin-bottom: 2rem;
}

.courses {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 4rem;
    margin-top: 1rem;
}

.courses-card {
    padding: 1rem;
    text-align: center;
    border: 1px solid rgba(128, 128, 128, 0.438);
    border-radius: 10px;
    position: relative;
}

.courses-card img {
    position: absolute;
    width: 180px;
    top: -4rem;
    left: 10%;
}

.courses-card h4 {
    margin-top: 7rem;
}

.courses-card p {
    margin: 0 0 1rem 0;
}

.courses-card:nth-child(3) img,
.courses-card:nth-child(4) img {
    width: 200px;
    left: 5%;
}

/* Ongoing Section */
.ongoing-events-container {
    max-width: 1200px;
    margin: auto;
    padding: 2rem 2rem 4rem 2rem;
}

.common-title {
    text-align: center;
    color: var(--pink);
    font-size: 1.8rem;
}

.ongoing-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 2rem;
    background-color: var(--bg-color);
    border-radius: 10px;
    padding: 2rem 2rem 3rem 2rem;
    position: relative;
}

.ongoing-container div {
    color: white;
    width: 100%;
}

.ongoing-events img {
    width: 12rem;
}

.ongoing-events h2 {
    margin-top: 4rem;
    font-size: 3rem;
}

.ongoing-events h3 {
    font-size: 1.8rem;
}

.ongoing-events .become-expert {
    margin: 1rem 0 3rem 0;
    font-size: 1.2rem;
}

.ongoing-events .tag {
    margin-top: 2rem;
    font-size: .8rem;
    position: absolute;
    bottom: .5rem;
}

.ongoing-events-img {
    display: flex;
    justify-content: center;
    align-items: center;
}

.ongoing-events-img img {
    width: 100%;
}

.course-icon {
    text-align: center;
    color: var(--mainColor);
}

/* Career Mistake Section */
.career-mistakes-container {
    max-width: 1200px;
    margin: auto;
    text-align: center;
    padding: 2rem;
}

.mistakes-title-para {
    margin-bottom: 2rem;
}

.mistakes-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--mainColor);
    border-radius: 10px;
    color: white;
}

.mistakes-container div {
    width: 100%;
    position: relative;
}

.mistakes-img img:first-child {
    width: 10rem;
    position: absolute;
    left: .5rem;
    top: .5rem;
}

.mistakes-img img:nth-child(2) {
    width: 40%;
    position: relative;
    bottom: -1rem;
    margin-top: 2rem;
}

.mistakes-text h2 {
    font-size: 2.5rem;
}

.mistakes-text p:nth-child(3) {
    color: var(--pink);
}

.youtube-container {
    padding-bottom: 56.25%;
    position: relative;
    display: block;
    width: 100%
}

.youtube-container iframe {
    position: absolute;
    top: 0;
    left: 0;
}

.youtube-container img {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 10rem;
}

/* Skido Partnership  */

.skido-partnership {
    max-width: 1200px;
    margin: auto;
    padding: 3rem 2rem 3rem 2rem;
}

.skido-partnership h1 {
    color: var(--pink);
    text-align: center;
}

.skido-partnership-container {
    margin-top: 3rem;
    height: 30rem;
    position: relative;
}

.skido-partnership-container div {
    background-color: var(--mainColor);
    color: white;
    font-size: 2rem;
    border-radius: 50%;
    text-align: center;
    position: absolute;
}

.skido-partnership-container div p {
    position: absolute;
    bottom: 1.5rem;
    width: 100%;
}

.partnership-first-box {
    width: 340px;
    height: 340px;
    bottom: 0;
}

.partnership-second-box {
    width: 320px;
    height: 320px;
    top: 0;
    left: 34%;
}

.partnership-third-box {
    width: 340px;
    height: 340px;
    right: 0;
    bottom: 0;
}

.mobile-number {
    margin-top: 3rem;
    text-align: center;
}

.mobile-number input {
    width: 300px;
    height: 2.3rem;
    outline: none;
    border-radius: 10px;
    border: 1px solid var(--mainColor);
    text-align: center;
    color: var(--mainColor);
    margin-right: 1rem;
}

.mobile-number input::placeholder {
    text-align: center;
    color: var(--mainColor);
    font-size: 1rem;
}

.mobile-number .shadow {
    box-shadow: 0 5px 5px #80808027;
}

/* Mentor Section  */

.Mentor-section {
    max-width: 1200px;
    padding: 5rem 2rem 5rem 2rem;
    text-align: center;
    margin: auto;
}

.mentor-title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: var(--mainColor);
    list-style-type: none;
    font-size: 2rem;
    gap: 2rem;
    margin: 3rem 0 10rem 0;
}

.mentor-details {
    background-color: var(--mainColor);
    position: relative;
    color: white;
    min-height: 16rem;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
    padding: 2rem;
}

.mentor-details img {
    position: absolute;
    top: -7rem;
    width: 200px;
}

.bold {
    font-weight: bold;
}

.mentor-details h3 {
    font-size: 1.5rem;
}

.para-size-common {
    font-size: 1.3rem;
}

.Mentor-section .gap {
    margin-top: 3rem;
}

.mentor-description {
    background-color: var(--mainColor);
    border-radius: 10px;
    color: white;
    display: flex;
    justify-content: end;
    align-items: center;
    margin-top: 2rem;
    position: relative;
}

.mentor-img {
    width: 200px;
    position: absolute;
    bottom: -1.5rem;
    left: 0;
}

.mentor-img img {
    width: 100%;
}

.mentor-des-details {
    padding: 4rem 2rem 2rem 2rem;
    width: 70%;
    right: 0;
}

.mentor-des-details h3 {
    font-size: 1.5rem;
    padding-bottom: 1rem;
}

/* counselling Section */
.counselling-container {
    max-width: 1200px;
    margin: auto;
    padding: 3rem 2rem 3rem 2rem;
    color: var(--mainColor);
}

.counselling {
    border: 1px solid var(--mainColor);
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    padding: 1rem;
}

.counselling div {
    width: 100%;
}

.counselling-details p:nth-child(2) {
    margin: 1rem 0 3rem 0;
}

.counselling img {
    width: 30%;
}


/* Blog Section  */
.blog-container {
    max-width: 1200px;
    margin: auto;
    text-align: center;
    padding: 2rem;
}

.cards-container {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 2rem;
    padding: 2rem;
}

.blog-cards {
    border-radius: 10px;
    border: 1px solid var(--mainColor);
    padding: 2rem 0 2rem 0;
}

.blog-cards p {
    margin-bottom: 3rem;
}