@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

:root {
  --mainColor: #015261;
  --pink: #c48194;
  --bg-color: #58bc85;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: content-box;
  font-family: 'Roboto', sans-serif;
}

a {
  color: white;
  text-decoration: none;
}

/* Tablet Size */
@media (max-width: 920px) {


  .hero-section img {
    position: relative;
    margin: auto;
    width: 80vw;
  }

  .course-container {
    flex-direction: column;
  }

  .courses {
    margin: auto;
    width: 100vw;
    /* grid-template-columns: 300px 300px !important; */
  }

  .ongoing-events h2 {
    font-size: 2rem;
  }

  .skido-partnership-container {
    height: 100% !important;
  }

  .skido-partnership-container div {
    position: relative !important;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: 20% !important;
    margin-top: 10px;
  }

  .partnership-first-box {
    width: 60vw;
    height: 60vw;
  }

  .partnership-second-box {
    width: 58vw;
    height: 58vw;
  }

  .partnership-third-box {
    width: 55vw;
    height: 55vw;
  }

  .mentor-details h3 {
    font-size: 1.5rem;
  }

  .mentor-img img,
  .counselling img {
    width: 20rem;
    height: auto;
  }

  .counselling div,
  .counselling img {
    width: 100%;
  }
}

@media (max-width: 560px) {
  .hero-section h1 {
    font-size: 1.2rem !important;
  }

  .mistakes-container {
    flex-direction: column;
    gap: 2rem;
    padding-bottom: 2rem;
  }

  .skido-partnership-container div {
    left: 5% !important;
  }

  .ongoing-container,
  .mentor-description,
  .counselling {
    flex-direction: column;
  }

  .laptop {
    width: 100%;
  }

  .ongoing-events h2 {
    font-size: 7vw;
  }

  .mentor-img {
    display: none !important;
  }

  .mobile-number {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .common-title {
    font-size: 1.4rem;
  }

  .skido-partnership h1 {
    font-size: 1.4rem;
  }
}